import React, {useState} from 'react'
import {createTheme, ThemeProvider, Typography, Grid, Container, TextField, Button, Modal } from '@mui/material'

import Box from '@mui/joy/Box';
import { Parallax, ParallaxProvider } from 'react-scroll-parallax';
// import Image from 'mui-image';

const theme = createTheme({
    palette:{

    },
    typography:{
        fontFamily:"Figtree"
    }
})





const Banner = () => {

    const [email, setEmail] = useState('');
    const [name, setName] = useState('');
    const [companyName, setCompanyName] = useState('');
    const [message, setMessage] = useState('');

    const [isValidEmail, setIsValidEmail] = useState(true);
    const [isValidName, setIsValidName] = useState(true);
    const [isValidCompanyName, setIsValidCompanyName] = useState(true);
    const [isValidMessage, setIsValidMessage] = useState(true);
    const [finalModal, setFinalModal] = useState(false);
    const [openModal, setOpenModal] = useState(false);

    // const handleOpenModal = () => {
    //     setOpenModal(true)
    //   }
      const handleClose = () => {
        setOpenModal(false)
        setFinalModal(false)
      }

    const emailChange = (event)=>{
        setEmail(event.target.value)
        setIsValidEmail(true)
      }
      const nameChange = (event) =>{
        setName(event.target.value)
        setIsValidName(true)
      }
      const companyNameChange = (event) =>{
        setCompanyName(event.target.value)
        setIsValidCompanyName(true)
      }
      const messageChange = (event) => {
        setMessage(event.target.value)
        setIsValidMessage(true)
      }
      const submitRequest = async () =>{
        console.log("type of name:-",typeof(message))
        try{
          fetch(`http://www.aitessa.com/api/save/`,{
            method:"POST",
            body:JSON.stringify({
              name:name,
              email:email,
              companyName:companyName,
              additionalDetails:message
            }),
            headers:{
              'Content-Type': 'application/json; charset=UTF-8',
            }
          }).then(response=>response.json()).then(response=>console.log(response))
          setEmail('');
          setName('');
          setCompanyName('');
          setMessage('');
          setFinalModal(true)
        }catch(error){
          console.log(error)
        }
        
      }
      const checkValidation = () => {
        console.log('email = ', email)
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        let validationObj = {
          email: true,
          name: true,
          companyName:true,
          message:true
        }
        if(email.trim() === "" || !emailRegex.test(email)){
          validationObj.email = false;
        }
        if(name.trim() === ""){
          validationObj.name = false;
        }
        if(companyName.trim() === ""){
          validationObj.companyName = false;
        }
        if(message.trim() === ""){
          validationObj.message = false
        }
        if(Object.values(validationObj).indexOf(false) === -1){
          submitRequest();
          
        }else{
          setIsValidEmail(validationObj.email);
          setIsValidName(validationObj.name);
          setIsValidCompanyName(validationObj.companyName);
          setIsValidMessage(validationObj.message);
        }
      }


    const style = {
        position: 'relative',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 500,
        bgcolor: 'background.paper',
        
        borderRadius:"20px",
        boxShadow: 24,
        pt: 2,
        px: 4,
        pb: 3,
      };
  return (
    <ThemeProvider theme={theme}>
            
     
      <Box sx={{ pt:15, pb:15, backgroundImage: "url('/uploads/images/banner2.png')", width:"100%", backgroundSize:"100% auto"}}>
        <ParallaxProvider>
            <Parallax scaleY={-10}>
              <Container maxWidth="md" sx={{ml:{sm:0,md:0,lg:10}}} >
                  <Grid container>
                      <Grid item sm={2}></Grid>
                      <Grid item xs={12} sm={8} md={6} lg={6}  sx={{border:"2px solid #fff", pl:{xs:4,sm:7},pt:4,pb:3,pr:{xs:4, sm:0},mt:5, borderRadius:4, bgcolor:"#fff", boxShadow:24}}>
                          
                        <Typography variant="h5" sx={{mb:2}}>Your contact, our future collaboration !</Typography>
                          
                          <Grid item xs={12} sm={10} md={10} sx={{p:1 ,pl:0}}>
                            <TextField 
                                label="Name"
                                variant="outlined"
                                fullWidth
                                value={name}
                                onChange={nameChange}
                                error={!isValidName}
                                ></TextField>
                          </Grid> 
                          <Grid item xs={12} sm={10} md={10} sx={{p:1 ,pl:0}}>
                            <TextField 
                                label="Company Name"
                                variant="outlined"
                                fullWidth
                                value={companyName}
                                onChange={companyNameChange}
                                error={!isValidCompanyName}
                                ></TextField>
                          </Grid> 
                          <Grid item xs={12} sm={10} md={10} sx={{p:1 ,pl:0}}>
                            <TextField 
                                label="Email"
                                variant="outlined"
                                sx={{}}
                                fullWidth
                                value={email}
                                onChange={emailChange}
                                error={!isValidEmail}
                                ></TextField>
                          </Grid>
                          <Grid item xs={12} sm={10} md={10} sx={{p:1 ,pl:0}}>
                            <TextField 
                                label="Message"
                                variant="outlined"
                                sx={{}}
                                fullWidth
                                multiline
                                maxRows={5}
                                value={message}
                                onChange={messageChange}
                                error={!isValidMessage}
                                ></TextField>
                          </Grid>
                          <Grid item xs={12} sm={10} md={10} sx={{p:2 ,pl:0}}>
                              <Button variant='contained' sx={{bgcolor:"#004AAD"}} onClick={checkValidation}>Submit</Button>
                          </Grid>
                          
                      </Grid>  
                    <Modal open={finalModal} onClose={handleClose} aria-labelledby="parent-modal-title" aria-describedby="parent-modal-description">
        <Box sx={{ ...style, width: {xs:500,md:500}, bgcolor:"#004AAD",color:"#fff",p:5 }}>
          <Typography variant="h6">Your Request is successfully Submitted. We will contact you soon.</Typography>
        </Box>
        </Modal>
                      </Grid>
              
              </Container>
            </Parallax>

        
        </ParallaxProvider>
      </Box>
       
    </ThemeProvider>
  )
}

export default Banner